<template>
  <div class="mt-4 row">
    <div class="col-12">
      <div class="card shadow-lg">
        <!-- Card header -->
        <div class="card-header">
          <h5 class="mb-0">Atletas</h5>
        </div>
        <div class="d-flex justify-content-between m-3">
          <div class="col-3">
            <div class="input-group">
              <input v-model="name" type="text" class="form-control border-bottom border-dark input-icon"
                     placeholder="Buscar" aria-label="Recipient's username" aria-describedby="button-addon2">
            </div>
          </div>
          <div class="col-8 text-end">
            <button @click="openModal" class="btn btn-dark align-content-end">
              <i class="fa fa-plus-square me-2"></i> Nuevo
            </button>
          </div>
          <div class="col-1"></div>
        </div>
        <div class="table-responsive">
          <table id="datatable-basic" class="table table-flush">
            <thead class="thead-light">
            <tr>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Nombre
              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Edad
              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Peso (kg)
              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Deporte
              </th>
              <th
                class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7"
              >
                Acciones
              </th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in deportistas" :key="item.id"
                v-show="this.$store.state.provincia===item.provincia.id || $store.state.group==='Administrador'">
              <td class="text-sm font-weight-normal ps-4">{{ item.nombre }}</td>
              <td class="text-sm font-weight-normal ps-4">{{ item.edad }}</td>
              <td class="text-sm font-weight-normal ps-4">{{ item.peso }}</td>
              <td class="text-sm font-weight-normal ps-4">{{ item.deporte.nombre }}</td>
              <td class="text-sm font-weight-normal ps-4">
                <div class="btn-group" role="group">
                  <button
                    @click="openUpdate(item)"
                    data-bs-toggle="tooltip"
                    data-bs-placement="top"
                    title="Editar Marca" data-container="body" data-animation="true"
                    class="btn btn-info p-1 ms-1">
                    <i class="material-icons opacity-10">edit</i></button>
                  <button data-bs-toggle="tooltip" data-bs-placement="top"
                          title="Eliminar Marca" data-container="body" data-animation="true"
                          class="btn btn-danger p-1 ms-1">
                    <i class="material-icons opacity-10">delete</i></button>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="divModal Nuevo">
      <div v-if="showModal" :class="['modal', { 'show': showModal }]" @transitionend="onTransitionEnd">
        <div class="modal-content">
          <div class="row mb-3 border-bottom border-dark">
            <h4 class="text-start"><i class="fa fa-plus-square me-2"></i>Nuevo deportista<i @click="closeModal"
                                                                                            class="material-icons-round opacity-10 modal-icon">close</i>
            </h4>
          </div>
          <div class="row mb-3">
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Nombre y apellidos</label>
              <input v-model="nombre" class="form-control border-bottom border-dark p-2 mb-3"
                     type="text">
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Número de identidad</label>
              <input v-model="ci" class="form-control border-bottom border-dark p-2 mb-3"
                     type="text">
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">País</label>
              <select v-model="pais" class="form-control border-bottom border-dark p-2 mb-3">
                <option value="0" disabled>Seleccione un país</option>
                <option v-for="item in paises" :key="item.id" :value="item.id">{{ item.pais }}</option>
              </select>
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Peso (Kg)</label>
              <input v-model="peso" class="form-control border-bottom border-dark p-2 mb-3"
                     type="number">
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Estatura (cm)</label>
              <input v-model="estatura" class="form-control border-bottom border-dark p-2 mb-3"
                     type="number">
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Sexo</label>
              <select v-model="sexo" class="form-control border-bottom border-dark p-2 mb-3">
                <option value="" disabled>Seleccione el sexo</option>
                <option value="Femenino">Femenino</option>
                <option value="Masculino">Masculino</option>
              </select>
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Fecha de nacimiento</label>
              <VueDatePicker v-model="fecha_nacimiento" :enable-time-picker="false" placeholder="Seleccione una fecha"
                             auto-apply locale="es" class="form_fecha mb-3" />
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Año en el deporte</label>
              <VueDatePicker v-model="anno_deporte" year-picker auto-apply />
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Año en el equipo nacional</label>
              <VueDatePicker v-model="anno_nacional" year-picker auto-apply />
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Deporte</label>
              <select v-model="deporte" class="form-control border-bottom border-dark p-2 mb-3">
                <option value="0" disabled>Seleccione un deporte</option>
                <option v-for="item in deportes" :key="item.id" :value="item.id">{{ item.nombre }}</option>
              </select>
            </div>
            <div class="col-lg-6 col-md-6 col-sm-12 mb-3 servicios">
              <label class="form-label ms-1">Disciplinas</label>
              <select class="form-control shadow-none rounded-0 text-black" v-model="disciplina"
                      :disabled="deporte===0">
                <option value="0" disabled>Seleccione las disciplinas</option>
                <option v-for="item in disciplinas" :key="item.id" :value="item.id" v-show="item.deporte.id===deporte"
                        :disabled="this.disciplinas_selected.includes(item.id)">{{ item.nombre }} {{ item.codigo }}
                </option>
              </select>
              <div class="selected-items">
                <div class="selected-item" v-for="item in disciplinas"
                     v-show="disciplinas_selected.includes(item.id)">
                  <span> {{ item.nombre }} {{ item.codigo }} </span>
                  <span class="remove-item" @click="Quitar(item.id)">x</span>
                </div>
              </div>
            </div>
            <div v-show="error" class="text-danger mt-3 text-center p-2"
                 style="background-color: rgba(255,19,28,0.08)">
              <div class="d-flex align-center justify-content-center"><i class="material-icons-round opacity-10 me-2">error</i>
                <p class="text-dark">No pueden haber campos vacíos</p></div>
            </div>
          </div>
          <div class="row">
            <div class="col-6 d-flex justify-content-start">
              <button @click="closeModal" class="btn btn-secondary" type="button">Cancelar</button>
            </div>
            <div class="col-6 d-flex justify-content-end">
              <button @click="Guardar" class="btn btn-dark" type="button">Guardar</button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="divModal Actualizar">
      <div v-if="showUpdate" :class="['modal', { 'show': showUpdate }]" @transitionend="onTransitionEnd">
        <div class="modal-content">
          <div class="row mb-3 border-bottom border-dark">
            <h4 class="text-start"><i class="fa fa-pencil-square me-2"></i>Actualizar deportista<i @click="closeUpdate"
                                                                                                   class="material-icons-round opacity-10 modal-icon">close</i>
            </h4>
          </div>
          <div class="row mb-3">
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Nombre y apellidos</label>
              <input v-model="nombre" class="form-control border-bottom border-dark p-2 mb-3"
                     type="text">
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Número de identidad</label>
              <input v-model="ci" class="form-control border-bottom border-dark p-2 mb-3"
                     type="text">
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">País</label>
              <select v-model="pais" class="form-control border-bottom border-dark p-2 mb-3">
                <option value="0" disabled>Seleccione un país</option>
                <option v-for="item in paises" :key="item.id" :value="item.id">{{ item.pais }}</option>
              </select>
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Peso (Kg)</label>
              <input v-model="peso" class="form-control border-bottom border-dark p-2 mb-3"
                     type="number">
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Estatura (cm)</label>
              <input v-model="estatura" class="form-control border-bottom border-dark p-2 mb-3"
                     type="number">
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Sexo</label>
              <select v-model="sexo" class="form-control border-bottom border-dark p-2 mb-3">
                <option value="" disabled>Seleccione el sexo</option>
                <option value="Femenino">Femenino</option>
                <option value="Masculino">Masculino</option>
              </select>
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Fecha de nacimiento</label>
              <VueDatePicker v-model="fecha_nacimiento" :enable-time-picker="false" placeholder="Seleccione una fecha"
                             auto-apply locale="es" class="form_fecha mb-3" />
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Año en el deporte</label>
              <VueDatePicker v-model="anno_deporte" year-picker auto-apply />
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Año en el equipo nacional</label>
              <VueDatePicker v-model="anno_nacional" year-picker auto-apply />
            </div>
            <div class="col-md-6 col-lg-4 col-sm-12">
              <label class="form-label">Deporte</label>
              <select v-model="deporte" class="form-control border-bottom border-dark p-2 mb-3">
                <option value="0" disabled>Seleccione un deporte</option>
                <option v-for="item in deportes" :key="item.id" :value="item.id">{{ item.nombre }}</option>
              </select>
            </div>
            <div class="col-lg-6 col-md-6 col-sm-12 mb-3 servicios">
              <label class="form-label ms-1">Disciplinas</label>
              <select class="form-control shadow-none rounded-0 text-black" v-model="disciplina"
                      :disabled="deporte===0">
                <option value="0" disabled>Seleccione las disciplinas</option>
                <option v-for="item in disciplinas" :key="item.id" :value="item.id" v-show="item.deporte.id===deporte"
                        :disabled="this.disciplinas_selected.includes(item.id)">{{ item.nombre }} {{ item.codigo }}
                </option>
              </select>
              <div class="selected-items">
                <div class="selected-item" v-for="item in disciplinas"
                     v-show="disciplinas_selected.includes(item.id)">
                  <span> {{ item.nombre }}</span>
                  <span class="remove-item" @click="Quitar(item.id)">x</span>
                </div>
              </div>
            </div>
            <div v-show="error" class="text-danger mt-3 text-center p-2"
                 style="background-color: rgba(255,19,28,0.08)">
              <div class="d-flex align-center justify-content-center"><i class="material-icons-round opacity-10 me-2">error</i>
                <p class="text-dark">No pueden haber campos vacíos</p></div>
            </div>
          </div>
          <div class="row">
            <div class="col-6 d-flex justify-content-start">
              <button @click="closeUpdate" class="btn btn-secondary" type="button">Cancelar</button>
            </div>
            <div class="col-6 d-flex justify-content-end">
              <button @click="Actualizar" class="btn btn-dark" type="button">Guardar</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import gql from "graphql-tag";
import Swal from "sweetalert2";

const BUSCAR = gql`
query Deportistas($name:String!) {
    deportistas(name: $name) {
        id
        nombre
        edad
        estatura
        peso
        sexo
        foto
        ci
        annoDeporte
        annoNacional
        fechaNacimiento
        codigo
        isActive
        pais {
            id
            pais
            siglas
        }
        provincia {
                id
                nombre
                siglas
            }
        deporte {
            id
            nombre
            siglas
        }
        deportistadisciplinaSet {
            id
            disciplina {
                id
                nombre
                codigo
                deporte {
                    id
                    nombre
                    siglas
                }
            }
        }
    }
}
`;
const NUEVO = gql`
mutation NuevoDeportista(
    $annoDeporte:String!
    $annoNacional:String!
    $ci:String!
    $disciplina:[ID!]
    $fechaNacimiento:Date!
    $nombre:String!
    $pais:ID!
    $sexo:String!
    $edad:Int!
    $peso:Decimal!
    $estatura:Decimal!
    $deporte:ID!
    $provincia:ID!
) {
    nuevoDeportista(
        annoDeporte: $annoDeporte
        annoNacional: $annoNacional
        ci: $ci
        disciplina: $disciplina
        fechaNacimiento: $fechaNacimiento
        nombre: $nombre
        pais: $pais
        sexo: $sexo
        edad: $edad
        peso: $peso
        estatura:$estatura
        deporte:$deporte
        provincia:$provincia
    ) {
        success
        errors
    }
}`;
const ACTUALIZAR = gql`
mutation NuevoDeportista(
    $annoDeporte:String!
    $annoNacional:String!
    $ci:String!
    $disciplina:[ID!]
    $fechaNacimiento:Date!
    $nombre:String!
    $pais:ID!
    $sexo:String!
    $edad:Int!
    $peso:Decimal!
    $id:ID!
    $deporte:ID!
    $estatura:Decimal!
) {
    updateDeportista(
        annoDeporte: $annoDeporte
        annoNacional: $annoNacional
        ci: $ci
        disciplina: $disciplina
        fechaNacimiento: $fechaNacimiento
        nombre: $nombre
        pais: $pais
        sexo: $sexo
        edad: $edad
        peso: $peso
        id:$id
        estatura:$estatura
        deporte:$deporte
    ) {
        success
        errors
    }
}`;
const DISCIPLINAS = gql`
query Disciplinas {
    disciplinas(name: "") {
        id
        nombre
        codigo
        deporte {
            id
            nombre
            siglas
        }
    }
}
`;
const DEPORTES = gql`
query Deportes {
    deportes(name: "") {
        id
        nombre
        siglas
    }
}`;
const PAISES = gql`
query Paises {
    paises(name: "") {
        id
        pais
        siglas
    }
}`;
export default {
  name: "Atletas",
  data() {
    return {
      deportistas: [],
      paises: [],
      disciplinas: [],
      deportes: [],
      deporte: 0,
      disciplina: 0,
      name: "",
      showModal: false,
      error: false,
      nombre: "",
      edad: 0,
      estatura: 0,
      peso: 0,
      ci: "",
      pais: 0,
      sexo: "",
      fecha_nacimiento: null,
      anno_deporte: null,
      anno_nacional: null,
      id: 0,
      showUpdate: false,
      disciplinas_selected: []
    };
  },
  apollo: {
    deportistas: {
      query: BUSCAR,
      variables() {
        return {
          name: this.name
        };
      },
      fetchPolicy: "cache-and-network"
    },
    deportes: {
      query: DEPORTES,
      fetchPolicy: "cache-and-network"
    },
    disciplinas: {
      query: DISCIPLINAS,
      fetchPolicy: "cache-and-network"
    },
    paises: {
      query: PAISES,
      fetchPolicy: "cache-and-network"
    }
  },
  watch: {
    disciplina(value) {
      this.disciplinas_selected.push(value);
    }
  },
  methods: {
    async Guardar() {
      if (this.nombre === "" || this.peso === 0 || this.estatura === 0 || this.disciplinas_selected.length === 0 ||
        this.anno_deporte === null || this.fecha_nacimiento === null || this.ci === "") {
        this.error = true;
        return false;
      }
      const fecha = new Date();
      this.edad = fecha.getFullYear() - this.fecha_nacimiento.getFullYear();
      await this.$apollo.mutate({
        mutation: NUEVO,
        variables: {
          annoDeporte: this.anno_deporte,
          annoNacional: this.anno_nacional ? this.anno_nacional : "0",
          ci: this.ci,
          disciplina: this.disciplinas_selected,
          fechaNacimiento: this.convertirADate(this.fecha_nacimiento),
          nombre: this.nombre,
          pais: this.pais,
          sexo: this.sexo,
          edad: this.edad,
          peso: this.peso,
          deporte: this.deporte,
          estatura: this.estatura,
          provincia: this.$store.state.provincia
        },
        refetchQueries: [{ query: BUSCAR, variables: { name: this.name } }]
      }).then(response => {
        if (response.data.nuevoDeportista.success) {
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            }
          });
          Toast.fire({
            icon: "success",
            title: "Creado correctamente"
          });
        } else {
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            }
          });
          Toast.fire({
            icon: "error",
            title: response.data.nuevoDeportista.errors
          });
        }
        this.closeModal();
      }).catch(error => {
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 3000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          }
        });
        Toast.fire({
          icon: "error",
          title: error
        });
        this.closeModal();
      });
    },
    async Actualizar() {
      if (this.nombre === "" || this.edad === 0 || this.peso === 0 || this.estatura === 0 || this.disciplinas_selected.length === 0 ||
        this.anno_deporte === null || this.fecha_nacimiento === null || this.ci === "") {
        this.error = true;
        return false;
      }
      const fecha = new Date();
      await this.$apollo.mutate({
        mutation: ACTUALIZAR,
        variables: {
          annoDeporte: this.anno_deporte,
          annoNacional: this.anno_nacional,
          ci: this.ci,
          disciplina: this.disciplinas_selected,
          fechaNacimiento: this.convertirADate(this.fecha_nacimiento),
          nombre: this.nombre,
          pais: this.pais,
          sexo: this.sexo,
          edad: this.edad,
          deporte: this.deporte,
          peso: this.peso,
          estatura: this.estatura,
          id: this.id
        },
        refetchQueries: [{ query: BUSCAR, variables: { name: this.name } }]
      }).then(response => {
        if (response.data.updateDeportista.success) {
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            }
          });
          Toast.fire({
            icon: "success",
            title: "Actualizado correctamente"
          });
        } else {
          const Toast = Swal.mixin({
            toast: true,
            position: "top-end",
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
            didOpen: (toast) => {
              toast.addEventListener("mouseenter", Swal.stopTimer);
              toast.addEventListener("mouseleave", Swal.resumeTimer);
            }
          });
          Toast.fire({
            icon: "error",
            title: response.data.updateDeportista.errors
          });
        }
        this.closeUpdate();
      }).catch(error => {
        const Toast = Swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 3000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", Swal.stopTimer);
            toast.addEventListener("mouseleave", Swal.resumeTimer);
          }
        });
        Toast.fire({
          icon: "error",
          title: error
        });
        this.closeUpdate();
        console.error("La mutación falló:", error);
      });
    },
    openModal() {
      this.showModal = true;
      this.error = false;
      document.body.classList.add("modal-open");
    },
    openUpdate(item) {
      this.id = item.id;
      this.nombre = item.nombre;
      this.edad = item.edad;
      this.estatura = item.estatura;
      this.peso = item.peso;
      this.deporte = item.deporte ? item.deporte.id : 0;
      item.deportistadisciplinaSet.forEach(item => {
        this.disciplinas_selected.push(item.disciplina.id);
      });
      console.log(this.disciplinas_selected);
      this.fecha_nacimiento = item.fechaNacimiento;
      this.anno_deporte = item.annoDeporte;
      this.anno_nacional = item.annoNacional;
      this.sexo = item.sexo;
      this.pais = item.pais.id;
      this.ci = item.ci;
      this.showUpdate = true;
      this.error = false;
      document.body.classList.add("modal-open");
    },
    closeModal() {
      this.deporte = 0;
      this.disciplina = 0;
      this.nombre = "";
      this.edad = 0;
      this.estatura = 0;
      this.peso = 0;
      this.ci = "";
      this.pais = 0;
      this.sexo = "";
      this.fecha_nacimiento = null;
      this.anno_deporte = null;
      this.anno_nacional = null;
      this.id = 0;
      this.showModal = false;
      this.error = false;
      this.disciplinas_selected = [];
      document.body.classList.remove("modal-open");
    },
    closeUpdate() {
      this.deporte = 0;
      this.disciplina = 0;
      this.nombre = "";
      this.edad = 0;
      this.estatura = 0;
      this.peso = 0;
      this.disciplinas_selected = [];
      this.ci = "";
      this.pais = 0;
      this.sexo = "";
      this.fecha_nacimiento = null;
      this.anno_deporte = null;
      this.anno_nacional = null;
      this.id = 0;
      this.showUpdate = false;
      this.error = false;
      document.body.classList.remove("modal-open");
    },
    onTransitionEnd(event) {
      if (event.propertyName === "opacity" && !this.showModal) {
        this.$emit("close");
      }
    },
    convertirADate(fecha) {
      const fechaDate = new Date(fecha); // Crea un objeto Date a partir de la cadena datetime
      const dia = fechaDate.getDate();
      const mes = fechaDate.getMonth() + 1; // ¡Recuerda que los meses en JavaScript van de 0 a 11!
      const anio = fechaDate.getFullYear();
      return `${anio}-${mes < 10 ? "0" : ""}${mes}-${dia < 10 ? "0" : ""}${dia}`;
    },
    Quitar(id) {
      let newArreglo = [];
      this.disciplinas_selected.forEach(item => {
        if (item !== id) {
          newArreglo.push(item);
        }
      });
      this.disciplinas_selected = newArreglo;
      newArreglo = [];
    }
  }
};
</script>

