import DefaultLineChart from "@/examples/Charts/DefaultLineChart.vue";

const BUSCAR = gql`
query Pruebas($name:String!) {
    pruebas(name: $name) {
        id
        fecha
        archivo
        etapa
        observaciones
        valoracion
        deportista {
            id
            nombre
            edad
            peso
            estatura
            sexo
            foto
            ci
            annoDeporte
            annoNacional
            fechaNacimiento
            codigo
            isActive
            provincia {
                id
                nombre
                siglas
            }
            deporte {
                    id
                    nombre
                    siglas
                }
        }
        lugar {
            id
            nombre
            isActive
        }
    }
}
`;
const NUEVO = gql`
mutation NuevaPrueba(
    $deportista:ID!
    $fecha:Date!
    $lugar:ID!
    $etapa:String!
) {
    nuevaPrueba(
        deportista: $deportista
        fecha: $fecha
        lugar: $lugar
        etapa:$etapa
    ) {
        success
        errors
    }
}`;
const ACTUALIZAR = gql`
mutation UpdatePrueba(
    $deportista:ID!
    $fecha:Date!
    $lugar:ID!
    $observaciones:String!
    $valoracion:String!
    $id:ID!
    $etapa:String!
) {
    updatePrueba(
        deportista: $deportista
        fecha: $fecha
        lugar: $lugar
        observaciones: $observaciones
        valoracion: $valoracion
        id:$id
        etapa:$etapa
    ) {
        success
        errors
    }
}`;
