        peso
        estatura
        sexo
        foto
        ci
        annoDeporte
        annoNacional
        fechaNacimiento
        codigo
        isActive
        pais {
            id
            pais
            siglas
        }
        deporte {
                id
                nombre
                siglas
            }
        provincia {
            id
            nombre
            siglas
        }
    }
}
`;
