        tiempoSeis: $tiempoSeis
        tiempoTres: $tiempoTres
        tiempoUno: $tiempoUno
    ) {
        success
        errors
    }
}`;
const UPDATE_RAST = gql`
mutation UpdateRast (
    $distancia:Decimal!
