        presionSistolicaAntes: $presionSistolicaAntes
        presionSistolicaDespues: $presionSistolicaDespues
        prueba: $prueba
    ) {
        success
        error
    }
}
`;
const UPDATE_CARLSON = gql`
mutation UpdateCarlson (
