      showUpdate: false,
      ventana: true,
      etapa: "",
      prueba: "",
      distancia: 0,
      tiempo1: 0,
      tiempo2: 0,
      tiempo3: 0,
      tiempo4: 0,
      tiempo5: 0,
      tiempo6: 0,
      curva: false,

