      presionDiastolicaAntes: 0,
      contactosUno: 0,
      contactosDos: 0,
      contactosTres: 0,
      contactosQuatro: 0,
      contactosCinco: 0,
      contactosSeis: 0,
      contactosSiete: 0,
      contactosOcho: 0,
      contactosNueve: 0,
      contactosDiez: 0,
      frecuenciaCardiacaDespues: 0,
      presionSistolicaDespues: 0,
