      carlson_id: 0

    };
  },
  apollo: {
    pruebas: {
      query: BUSCAR,
      variables() {
        return {
          name: this.name
        };
      },
      fetchPolicy: "cache-and-network"
    },
    lugares: {
      query: LUGARES,
      fetchPolicy: "cache-and-network"
    },
    deportistas: {
      query: ATLETAS,
      fetchPolicy: "cache-and-network"
    },
    carlson: {
